.my-work {
  background-color: var(--clr-dark);
  color: var(--clr-light);
  text-align: center;
}

.section__subtitle--my-work {
  color: var(--clr-accent);
  font-weight: var(--fw-reg);
  margin-top: 1em;
  margin-bottom: 2em;
}
.hover-icon__single {
  position: absolute;
  top: 48%;
  left: 43%;
}

.hover-icon {
  position: absolute;
  top: 50%;
  left: 27%;
  min-width: 10em;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.font-awesome-icon {
  color: var(--clr-dark);
  font-size: 3rem;
  cursor: pointer;
}

.portfolio {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  column-gap: 1em;
  row-gap: 1em;
}

.portfolio__item {
  background: var(--clr-accent);
  overflow: hidden;
  position: relative;
}

.portfolio__img {
  transition: transform 750ms cubic-bezier(0.5, 0, 0.5, 1), opacity 250ms linear;
}

.portfolio__img:hover {
  transform: scale(1.2);
  opacity: 0.75;
}
